                "step": 1,
                "description": "Fr die Fllung die Kartoffeln schlen, waschen und in Salzwasser gar kochen. Die heien Kartoffeln zu einer glatten Masse verarbeiten. Die Zwiebel hacken und in 1 EL Butter goldgelb rsten. Den Quark glatt rhren. Kartoffeln, Quark und die Zwiebeln zusammen mit dem Fett in eine Schssel geben, sehr gut glatt verrhren und mit Salz und Pfeffer abschmecken. Der Teig soll keine Quark- oder Kartoffelklmpchen enthalten. ",
